.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.title-display {
    font-size: 8vw;
}

.timer-list[data-is-current='true'] {
    background-color: lime;
}

.timer-list {
    background-color: white;
    text-align: center !important;
}

.time-display {
    font-size: 20vmin;
    display: inline-block;
    min-width: 100vmin;
    width: 100vmin;
}


.time-display[data-is-finish="true"] {
    background-color: red;
}

.control-buttons {
    display: inline-block;
    white-space: nowrap;
    width: 200px;
}

.button {
    display: inline-block;
    /*width: 90px;*/
}

.adsense {
    margin-top: 15px;
    margin-bottom: 15px;
}